.bio-container {
    font-family: 'Josefin Sans', sans-serif;
    color: white;
    padding: 20px;
    background-color: #3b3b3b;
    border: 1px solid #ccc;
    border-radius: 8px;
    width: 100%;
    max-width: 720px;
    margin: 10px;

}

#title {
    padding-bottom: 20px;
    text-align: center;
}

.profile-photo {
    transform: scale(1.2);
    position: relative;
    margin: 0 auto;
    display: block;
    top: 0;
}

.buttons {
    margin: 0px 0;
    text-align: center;
}

#linkd {
    background-color: #0073b1; /* LinkedIn Blue */
}

#gith {
    background-color: #333; /* GitHub Dark */
}

#gith:hover {
    background-color: #333; /* GitHub Dark */
}

button:hover {
    opacity: 0.8;
}

.profile-photo {
    
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin-top: 15px;
    margin-bottom: 30px;
}
.email-button {
    display: inline-block;
    margin-top: 10px;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #f75050; /* Red */
    color: white;
    cursor: pointer;
    text-decoration: none; /* Remove underline */
    text-align: center; /* Center text */
}

.email-button:hover {
    opacity: 0.8;
}


@media only screen and (max-width: 768px) {
    .bio-container {
        width: 90%;
        margin-left: -5px;
        
        
    }
    

    .profile-photo {
        position: static;
        transform: scale(1);
        margin: 0 auto;
        display: block;
    }

    #title {
        padding-bottom: 10px;
    }

    .buttons {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .buttons button {
        width: 80%;
        margin: 5px 0;
    }
}

